import { Connection, PublicKey } from '@solana/web3.js';

export class UI {
  constructor() {
    this.initElements();
    this.setupBlockchainIntegration();
  }
  
  initElements() {
    this.healthFill = document.getElementById('healthFill');
    this.healthText = document.getElementById('healthText');
    this.timerText = document.getElementById('timerText');
    this.scoreText = document.getElementById('scoreText');
    this.leaderboardContent = document.getElementById('leaderboardContent');
    this.gameOverScreen = document.getElementById('gameOverScreen');
    this.finalScore = document.getElementById('finalScore');
    this.timeSurvived = document.getElementById('timeSurvived');
    
    // Blockchain elements
    this.connectWalletBtn = document.getElementById('connectWalletBtn');
    this.walletStatus = document.getElementById('walletStatus');
    this.walletAddress = document.getElementById('walletAddress');
  }
  
  setupBlockchainIntegration() {
    this.isWalletConnected = false;
    this.playerName = 'Player';
    this.connection = new Connection('https://rpc.gorbagana.wtf', 'confirmed'); // Gorbagana Testnet RPC
    
    // Placeholder wallet addresses for AI opponents
    this.aiWalletAddresses = [
      'GhXsT..cGD2',
      'hGWh3..ghG6'
    ];
    
    // Bind wallet connection to button
    this.connectWalletBtn.addEventListener('click', () => this.connectWallet());
    
    // Check for existing wallet connection
    this.checkExistingWalletConnection();
    
    // Log for debugging
    console.log('Blockchain integration initialized with RPC: https://rpc.gorbagana.wtf');
  }
  
  async checkExistingWalletConnection() {
    if (window.backpack && window.backpack.isConnected) {
      try {
        const publicKey = window.backpack.publicKey?.toString();
        if (publicKey) {
          console.log('Detected existing Backpack wallet connection:', publicKey);
          this.isWalletConnected = true;
          this.playerName = publicKey.slice(0, 6) + '...' + publicKey.slice(-4);
          this.updateWalletUI();
          console.log('Wallet UI updated:', { playerName: this.playerName, publicKey });
        } else {
          console.warn('Backpack wallet detected but no public key available');
        }
      } catch (error) {
        console.error('Error checking existing Backpack connection:', error.message || error);
      }
    } else if (window.solana && window.solana.isConnected) {
      try {
        const publicKey = window.solana.publicKey?.toString();
        if (publicKey) {
          console.warn('Detected non-Backpack wallet (possibly Phantom). Gorbagana Testnet requires Backpack.', { publicKey });
          alert('Non-Backpack wallet detected. Please use Backpack for Gorbagana Testnet compatibility.');
        } else {
          console.warn('Non-Backpack wallet detected but no public key available');
        }
      } catch (error) {
        console.error('Error checking existing wallet connection:', error.message || error);
      }
    } else {
      console.log('No Solana wallet detected.');
    }
  }
  
  async connectWallet() {
    if (this.isWalletConnected) {
      console.log('Wallet already connected:', this.playerName);
      return;
    }

    // Prioritize Backpack
    if (window.backpack) {
      try {
        await window.backpack.connect();
        const publicKey = window.backpack.publicKey.toString();
        console.log('Connected to Backpack wallet:', publicKey);
        this.isWalletConnected = true;
        this.playerName = publicKey.slice(0, 6) + '...' + publicKey.slice(-4);
        this.updateWalletUI();
        console.log('Wallet UI updated:', { playerName: this.playerName, publicKey });
      } catch (error) {
        console.error('Backpack wallet connection failed:', error.message || error);
        alert('Failed to connect to Backpack wallet. Please ensure Backpack is installed and try again.');
      }
    } else if (window.solana) {
      try {
        await window.solana.connect();
        const publicKey = window.solana.publicKey.toString();
        console.warn('Connected to non-Backpack wallet (possibly Phantom). Gorbagana Testnet requires Backpack.', { publicKey });
        alert('Non-Backpack wallet (e.g., Phantom) connected. Please use Backpack for Gorbagana Testnet compatibility.');
        this.isWalletConnected = true;
        this.playerName = publicKey.slice(0, 6) + '...' + publicKey.slice(-4);
        this.updateWalletUI();
        console.log('Wallet UI updated:', { playerName: this.playerName, publicKey });
      } catch (error) {
        console.error('Wallet connection failed:', error.message || error);
        alert('Failed to connect wallet. Please ensure Backpack is installed and try again.');
      }
    } else {
      console.log('No Solana wallet detected.');
      alert('Please install the Backpack wallet extension to play Garbage Grab.');
    }
  }
  
  updateWalletUI() {
    this.connectWalletBtn.textContent = 'WALLET CONNECTED';
    this.connectWalletBtn.style.background = 'linear-gradient(45deg, #00ff00, #00aa00)';
    this.connectWalletBtn.disabled = true;
    this.walletAddress.textContent = this.playerName;
    this.walletStatus.style.display = 'block';
  }
  
  updateHealth(health) {
    const percentage = Math.max(0, health);
    this.healthFill.style.width = percentage + '%';
    this.healthText.textContent = `${Math.round(health)}/100`;
    
    if (health > 60) {
      this.healthFill.style.background = 'linear-gradient(90deg, #00ff00, #88ff00)';
    } else if (health > 30) {
      this.healthFill.style.background = 'linear-gradient(90deg, #ffff00, #ff8800)';
    } else {
      this.healthFill.style.background = 'linear-gradient(90deg, #ff0000, #ff4400)';
    }
  }
  
  updateTimer(timeRemaining) {
    const minutes = Math.floor(timeRemaining / 60);
    const seconds = Math.floor(timeRemaining % 60);
    this.timerText.textContent = `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
    
    if (timeRemaining < 60) {
      this.timerText.style.color = '#ff0000';
      this.timerText.style.animation = 'pulse 1s infinite';
    } else if (timeRemaining < 120) {
      this.timerText.style.color = '#ffff00';
    } else {
      this.timerText.style.color = '#ff00ff';
      this.timerText.style.animation = 'none';
    }
  }
  
  updateScore(score) {
    this.scoreText.textContent = score.toString();
  }
  
  updateLeaderboard(leaderboard) {
    this.leaderboardContent.innerHTML = '';
    
    leaderboard.forEach((player, index) => {
      const item = document.createElement('div');
      item.className = `leaderboard-item rank-${index + 1}`;
      
      const rank = index + 1;
      const rankText = rank === 1 ? '🥇' : rank === 2 ? '🥈' : rank === 3 ? '🥉' : `${rank}.`;
      
      let displayName = player.name;
      if (player.name === 'Player') {
        displayName = this.playerName;
      } else if (player.name.startsWith('TrashBot_')) {
        const botIndex = parseInt(player.name.split('_')[1]) - 1;
        displayName = this.aiWalletAddresses[botIndex] || player.name;
      }
      
      item.innerHTML = `
        <span>${rankText} ${displayName}</span>
        <span>${player.score}</span>
      `;
      
      this.leaderboardContent.appendChild(item);
    });
  }
  
  showGameOver(finalScore, timeElapsed) {
    this.finalScore.textContent = finalScore;
    
    const minutes = Math.floor(timeElapsed / 60);
    const seconds = Math.floor(timeElapsed % 60);
    this.timeSurvived.textContent = `${minutes}:${seconds.toString().padStart(2, '0')}`;
    
    this.gameOverScreen.style.display = 'block';
  }
}

// Add pulse animation to CSS
const addPulseAnimationCSS = () => {
  if (!document.querySelector('#pulseAnimation')) {
    const style = document.createElement('style');
    style.id = 'pulseAnimation';
    style.textContent = `
      @keyframes pulse {
        0%, 100% { opacity: 1; }
        50% { opacity: 0.5; }
      }
    `;
    document.head.appendChild(style);
  }
};
// Initialize CSS when UI is created
addPulseAnimationCSS();